/* Modern Variables */
:root {
    --primary: #3b82f6;
    --primary-dark: #2563eb;
    --primary-light: #93c5fd;
    --text-dark: #1f2937;
    --text-light: #6b7280;
    --white: #ffffff;
    --gray-50: #f9fafb;
    --gray-800: #1f2937;
    --transition: all 0.3s ease;
  }
  
  /* Global Styles */
  body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
    color: var(--text-dark);
    line-height: 1.5;
  }
  
  /* Navigation */
  .nav {
    background: #388bfc;
    padding: 0.7rem 0;
    width: 100%;
    position: sticky;
    top: 0;
    z-index: 100;
  }
  
  .nav-container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    max-width: 1400px;
    margin: 0 auto;
    padding: 0;
    position: relative;
    height: 64px;
  }
  
  .nav-logo, .nav-logo::before, .nav-logo::after {
    border-left: none !important;
    box-shadow: none !important;
    background: none !important;
    content: none !important;
    display: inline-flex;
  }
  
  .nav-logo {
    display: flex;
    align-items: center;
    font-size: 1.65rem;
    font-weight: 800;
    color: #fff;
    margin-right: 0;
    letter-spacing: 1.2px;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    padding-left: 1.2rem;
  }
  
  .logo-icon {
    width: 1.7rem;
    height: 1.7rem;
    margin-right: 0.5rem;
  }
  
  .nav-links {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    gap: 2.1rem;
  }
  
  .nav-link {
    color: #fff;
    font-size: 1.08rem;
    font-weight: 600;
    text-decoration: none;
    transition: color 0.2s;
    letter-spacing: 0.5px;
  }
  
  .nav-link:hover {
    color: #e0e7ef;
  }
  
  .nav, .nav-container, .nav-links, .nav-link {
    border-right: none !important;
    box-shadow: none !important;
  }
  
  .nav-links::after, .nav-links::before, .nav::after, .nav::before, .nav-container::after, .nav-container::before {
    display: none !important;
    content: none !important;
  }
  
  /* Hero Section */
  .hero {
    background: linear-gradient(135deg, var(--primary), var(--primary-dark));
    padding: 8rem 2rem 4rem;
    color: var(--white);
  }
  
  .hero-container {
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    align-items: center;
  }
  
  .hero-content h1 {
    font-size: 3.5rem;
    line-height: 1.2;
    margin-bottom: 1.5rem;
    font-weight: 800;
  }
  
  .hero-buttons {
    display: flex;
    gap: 1rem;
    margin-top: 2rem;
  }
  
  .btn-primary {
    background: var(--white);
    color: var(--primary);
    padding: 1rem 2rem;
    border-radius: 0.5rem;
    font-weight: 600;
    transition: var(--transition);
    text-decoration: none;
  }
  
  .btn-secondary {
    background: transparent;
    border: 2px solid var(--white);
    color: var(--white);
    padding: 1rem 2rem;
    border-radius: 0.5rem;
    font-weight: 600;
    transition: var(--transition);
    text-decoration: none;
  }
  
  .hero-logo-bg {
    background: #1f2937; /* Navy */
    border-radius: 2rem;
    padding: 2.5rem 4.5rem; /* Increased left/right padding */
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 500px;      /* Increased from 340px */
    min-height: 340px;
    max-width: 600px;      /* Increased from 400px */
    margin: 0 auto;
    box-shadow: 0 8px 32px rgba(31, 41, 55, 0.18);
  }
  
  @media (max-width: 900px) {
    .hero-logo-bg {
      min-width: 250px;
      min-height: 200px;
      padding: 1.5rem 2rem;
    }
  }
  
  /* Services Section */
  .services {
    padding: 6rem 2rem;
    background: var(--gray-50);
  }
  
  .section-header {
    text-align: center;
    margin-bottom: 4rem;
  }
  
  .section-header p {
    max-width: 800px;
    margin: 0 auto;
    margin-bottom: 3rem;
    color: var(--text-light);
  }
  
  .section-title {
    text-align: center;
    margin-bottom: 4rem;
  }
  
  .section-title h2 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
  }
  
  .services-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
    max-width: 1200px;
    margin: 0 auto;
    padding-top: 2rem;
  }
  
  .service-card {
    background: var(--white);
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    transition: var(--transition);
    border-top: 4px solid var(--primary);
  }
  
  .service-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  }
  
  /* Testimonials */
  .testimonials {
    padding: 6rem 2rem;
    background: var(--gray-50);
  }
  
  .testimonial-card {
    background: var(--white);
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
    max-width: 800px;
    margin: 0 auto;
    text-align: left;
  }
  
  .testimonial-stars {
    display: flex;
    gap: 0.5rem;
    margin-bottom: 1rem;
    justify-content: flex-start;
  }
  
  .star-icon {
    width: 24px;
    height: 24px;
    color: #e5e7eb;  /* Default color for empty stars */
  }
  
  .star-icon.filled {
    color: #FFD700;  /* Gold color for filled stars */
    fill: currentColor;  /* This ensures the star is filled */
  }
  
  /* Contact Form */
  .contact {
    padding: 6rem 2rem;
  }
  
  .contact-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 4rem;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .contact-form {
    background: var(--white);
    padding: 2.5rem 2rem;
    border-radius: 1.25rem;
    box-shadow: 0 8px 32px rgba(59, 130, 246, 0.08);
    max-width: 450px;
    margin: 0 auto;
  }
  
  .contact-form h3 {
    font-size: 1.5rem;
    margin-bottom: 2rem;
    color: var(--primary-dark);
    font-weight: 700;
    text-align: center;
  }
  
  .form-group {
    margin-bottom: 1.5rem;
  }
  
  .form-input {
    width: 100%;
    padding: 0.85rem 1.1rem;
    border: 1.5px solid #e5e7eb;
    border-radius: 0.5rem;
    font-size: 1rem;
    background: #f9fafb;
    transition: border-color 0.2s, box-shadow 0.2s;
    margin-top: 0.5rem;
  }
  
  .form-input:focus {
    border-color: var(--primary);
    box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.15);
    outline: none;
  }
  
  .submit-button {
    width: 100%;
    background: var(--primary);
    color: var(--white);
    padding: 1rem 0;
    border: none;
    border-radius: 0.5rem;
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
    transition: background 0.2s, box-shadow 0.2s;
    margin-top: 0.5rem;
    box-shadow: 0 2px 8px rgba(59, 130, 246, 0.08);
  }
  
  .submit-button:hover {
    background: var(--primary-dark);
  }
  
  /* Footer */
  .footer {
    background: var(--gray-800);
    color: var(--white);
    padding: 4rem 2rem 2rem;
  }
  
  .footer-grid {
    max-width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 4rem;
  }
  
  .footer-links h3 {
    color: var(--white);
    margin-bottom: 1.5rem;
  }
  
  .footer-links a {
    color: #9ca3af;
    text-decoration: none;
    transition: var(--transition);
  }
  
  .footer-links a:hover {
    color: var(--white);
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .hero-container {
      grid-template-columns: 1fr;
      text-align: center;
    }
  
    .hero-buttons {
      justify-content: center;
    }
  
    .nav-links {
      display: none;
    }
  
    .mobile-menu {
      display: block;
    }
  }
  
  /* Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .animate-fade-in {
    animation: fadeIn 0.6s ease-out forwards;
  }
  
  /* About Section */
  .about {
    padding: 6rem 2rem;
    background: var(--white);
  }
  
  .about-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 4rem;
    max-width: 1200px;
    margin: 0 auto;
    align-items: center;
  }
  
  .about-image {
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  }
  
  .about-image img {
    width: 100%;
    height: auto;
    object-fit: cover;
    transition: transform 0.3s ease;
  }
  
  .about-image:hover img {
    transform: scale(1.05);
  }
  
  .about-content {
    padding: 2rem;
  }
  
  .about-content h2 {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    color: var(--text-dark);
  }
  
  .about-content p {
    color: var(--text-light);
    margin-bottom: 1.5rem;
    font-size: 1.1rem;
    line-height: 1.8;
  }
  
  .stats-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    margin-top: 3rem;
  }
  
  .stat-card {
    background: var(--gray-50);
    padding: 1.5rem;
    border-radius: 1rem;
    text-align: center;
    transition: transform 0.3s ease;
  }
  
  .stat-card:hover {
    transform: translateY(-5px);
  }
  
  .stat-number {
    font-size: 2.5rem;
    font-weight: 700;
    color: var(--primary);
    margin-bottom: 0.5rem;
  }
  
  .stat-label {
    color: var(--text-light);
    font-weight: 500;
  }
  
  @media (max-width: 768px) {
    .about-grid {
      grid-template-columns: 1fr;
    }
    
    .about-image {
      margin-bottom: 2rem;
    }
    
    .about-content {
      padding: 0;
    }
  }

  .about-image-duo {
    display: flex;
    gap: 1.5rem;
    align-items: center;
    justify-content: center;
  }
  
  .about-img {
    object-fit: contain;
    max-width: 100%;
    height: auto;
  }
  
  .about-img-hesham {
    max-width: 200px;
  }
  
  .about-img-drew {
    max-width: 250px;
  }
  
  @media (max-width: 768px) {
    .about-image-duo {
      flex-direction: column;
      gap: 1rem;
    }
    .about-img-hesham, .about-img-drew {
      max-width: 150px;
    }
  }

  @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Playfair+Display:wght@700&display=swap');

  .contact-info h2 {
    font-family: 'Playfair Display', serif;
    font-size: 2.5rem;
    font-weight: 700;
    color: var(--primary-dark);
    margin-bottom: 1.2rem;
  }

  .contact-info p {
    font-size: 1.25rem;
    font-family: 'Inter', sans-serif;
    color: var(--text-dark);
    margin-bottom: 1.2rem;
  }

  .contact-label {
    font-size: 1.1rem;
    font-weight: 600;
    color: var(--primary);
    font-family: 'Inter', sans-serif;
  }

  .contact-value {
    font-size: 1.3rem;
    font-family: 'Inter', sans-serif;
    color: var(--text-dark);
    font-weight: 500;
    margin-bottom: 1.2rem;
  }

  .contact-icon {
    width: 2rem;
    height: 2rem;
    color: var(--primary-dark);
    margin-right: 0.5rem;
  }

  /* Remove social icons from footer */
  .footer-bottom .social-links {
    display: none !important;
  }

  /* Gallery Section */
  .gallery {
    padding: 6rem 2rem;
    background: linear-gradient(135deg, #f9fafb 60%, #e0e7ef 100%);
    border-radius: 2.5rem;
    max-width: 1200px;
    margin: 4rem auto;
  }

  .gallery-grid {
    display: flex;
    flex-direction: column;
    gap: 3.5rem;
    max-width: 1000px;
    margin: 0 auto;
  }

  .gallery-pair-card {
    background: var(--white);
    border-radius: 2rem;
    box-shadow: 0 8px 32px rgba(31, 41, 55, 0.10);
    padding: 2.5rem 2rem 2rem 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 0;
  }

  .gallery-pair-images {
    display: flex;
    gap: 2.5rem;
    justify-content: center;
    align-items: flex-end;
    width: 100%;
  }

  .gallery-pair-image-col {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .gallery-img {
    width: 340px;
    max-width: 90vw;
    height: 240px;
    object-fit: cover;
    border-radius: 1.25rem;
    box-shadow: 0 2px 12px rgba(31, 41, 55, 0.10);
    border: none;
    margin-bottom: 0.7rem;
    background: none;
  }

  .gallery-pair-label {
    font-size: 1.1rem;
    font-weight: 700;
    color: var(--primary-dark);
    background: #f3f4f6;
    padding: 0.3rem 1.2rem;
    border-radius: 0.7rem;
    margin-top: 0.2rem;
    letter-spacing: 1px;
  }

  .gallery-img-moss {
    object-fit: contain !important;
    height: 300px !important;
    background: none !important;
  }

  @media (max-width: 900px) {
    .gallery-pair-images {
      flex-direction: column;
      gap: 1.5rem;
    }
    .gallery-img {
      width: 100%;
      max-width: 95vw;
      height: auto;
    }
  }